body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {

}


header {
    background-color: #282c34;
    min-height: 1vh;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    font-size: calc(8px + 1vmin);
    color: white;
    padding: 3px;
  }
  
  

footer {
    min-height: 1vh;
    min-width: 100%;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    font-size: calc(3px + 1vmin);
    color: black;
    bottom: 0px;
  }
  
  


.Module {


}

.statsContainer {
    display: flex;
    flex-wrap: wrap;
}

.statsElement {
    text-align: center;
    border: dotted;
    min-width: 7vh;
    max-width: 15vh;
    min-height: 1vh;
    padding: 1vh;
    margin: 0.2vh;
}

.ModuleList {
    display: inline;
    min-width: 25vh;
}

.ModuleList * {
    color: black;
}

.ModuleList  a {
    text-decoration: underline;
}

.ModuleList  ul {
    list-style: none;
    padding: 10px;
}
.moduleContainer {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 10px;
}
